<template>
  <div />
  
</template>

<script>
import places from 'places.js';

export default {
  props: {
    type: {
      type: String,
      required: false,
    },
  },
  data() {
    return { instance: null };
  },
  mounted() {
    // make sure Vue does not know about the input
    // this way it can properly unmount
    this.input = document.createElement('input')
    this.input.placeholder = 'Enter City'
    this.$el.appendChild(this.input);

    this.instance = places({
      apiKey: this.apiKey,
      appId: this.appId,
      type: this.type,
      container: this.input,
      language: "en",
      templates: {
      value: function(suggestion) {
        return suggestion.name;
      }
    }
    }).configure({
      type: 'city',
      aroundLatLngViaIP: false,
    });

    this.instance.on('change', e => {
      this.$emit('info', { 
        name: e.suggestion.name, 
        countryCode: e.suggestion.countryCode,
        lat: e.suggestion.latlng.lat, 
        lng: e.suggestion.latlng.lng
      })
      this.$emit('change', e);
    });
  },
  beforeDestroy() {
    this.instance.destroy();
  },
  watch: {
    type(newVal) {
      this.instance.configure({
        type: newVal,
      });
    },
  },
};
</script>