import axios from 'axios'

const state = {
    forecast: [],
    found: false,
    fLoad: false,
    missingCity: false,
    baseUrl: 'https://api.openweathermap.org',
    imgBaseUrl: 'https://openweathermap.org'
}

const actions = {
    weather ({ commit }, geo) {
        var name = geo.name
        var countryCode = geo.countryCode
        var appid = process.env.VUE_APP_WEATHER_API_APP_ID

        var base = `${state.baseUrl}/data/2.5/weather?q=${name},${countryCode}&type=like&&appid=${appid}&units=metric`

        if (!state.fLoad) {
            state.fLoad = true
        }


        axios.get(base)
        .then(res => {
            commit('storeForecast', res.data)
        })
        .catch(error => {
            if (error.response.status === 400 || error.response.status === 404) {
        
                commit('apiNotFound')
            }
        })            
    },
}

const mutations = {
    storeForecast(state, data) {
        state.forecast = data
        if (!state.found) {
            state.found = true
        }
        state.missingCity = false
    },
    apiNotFound(state) {
        state.found = false
        state.missingCity = true
    }
}

const getters = {
    forecast: state => state.forecast,
    found: state => state.found,
    fLoad: state => state.fLoad,
    missingCity: state => state.missingCity,
    imgBaseUrl: state => state.imgBaseUrl
}

export default {
    state,
    actions,
    mutations,
    getters
}