<script>

import axios from 'axios'

import { mapActions, mapGetters } from 'vuex'

export default {
    props: {
        geo: {
            type: [Array, Object],
            required: true,
        },
        up: {
            type: String,
            required: true
        }
    },
    computed: {
        ...mapGetters(['forecast', 'found', 'fLoad', 'missingCity', 'imgBaseUrl'])
    },
    methods: {
        ...mapActions(['weather', 'getFlagUrl', 'getImageUrl', 'getCelius']),


    getFlagUrl(country)
    {
        var lower = country.toLowerCase();

        return `${this.imgBaseUrl}/images/flags/${lower}.png`;

    },
    getImageUrl(icon) 
    {
        return `${this.imgBaseUrl}/img/wn/${icon}@2x.png`;
    }
    },
    watch: {
        up: function (newVal) 
        {
            if (newVal) {
                this.weather(this.geo)
            }
        }
    },
    mounted () {
    }
}
</script>

