<template>
  <div>

    <div class="flex flex-wrap  py-5 px-4 -mx-3 mt-6">
        <div class="w-full md:w-4/5 px-3 ">
            <app-places
            :type="type"
            v-on:info="transferInfo($event)"
            />
        </div>
        <div class="w-full md:w-1/6 px-3  md:px-0 lg:px-4 xs:px-4 mt-6 md:mt-0 lg:mt-0">
            <button class="py-3 text-xs md:text-sm w-full leading-tight text-white bg-indigo-600 rounded"
            @click.prevent="reqSearch"
            >
                Search
            </button>
        </div>
    </div>
  </div>
</template>

<script>
import AppPlaces from '@/components/partials/Places.vue';

export default {
  components: { AppPlaces },
  data() {
    return {
      appId: process.env.VUE_APP_ALGOLIA_APP_ID,
      apiKey: process.env.VUE_APP_ALGOLIA_API_KEY,
      type: 'city',
    };
  },
  methods: {

    transferInfo (e) {
        this.$emit('info', e)
    },

    reqSearch() {
        this.$emit('search', true);
    }
    
  },
};
</script>